@use "../../assets/sass/variables" as *;

.catalogue {
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  grid-auto-columns: 1fr;
  grid-auto-rows: 1fr;
  gap: 0px 0px;
  grid-auto-flow: row;
  grid-template-areas: "navbar main main main";
  &__navbar {
    grid-area: navbar;
  }
  &__main {
    grid-area: main;
  }
}
